<template>
  <div class="wrapper">
    <div class="w-seach" @click="handlePopShow">
      <p :title="curName.length>6?curName:''">{{curName}}</p>
    </div>
    <transition name="fade">
      <unitSeach v-if="popShow" :name="curName" @change="popClick"></unitSeach>
    </transition>
    <div class="content">
      <div class='c-left'>
        <ctitle :title="'参评情况'"></ctitle>
        <div class='cpl'>
          <liquidfill :params='cplParams'></liquidfill>
          <div class='cpl-font'>参评率</div>
        </div>
        <div class='cpl cpl3d'>
          <customized-pie :params="cpqkPieParams"></customized-pie>
        </div>
      </div>
      <div class='c-center'>
        <ctitle :title="'评价情况'"></ctitle>
        <div class='cpl'>
          <liquidfill :params='mylParams'></liquidfill>
          <div class='cpl-font'>满意率</div>
        </div>
        <div class='cpl cpl3d'>
          <customized-pie :params="pjqkPieParams"></customized-pie>
        </div>
      </div>
      <div class='c-right'>
        <ctitle :title="'市级信访部门满意率排名TOP10'"></ctitle>
        <div class='cpl'>
          <div class="w-echart" id="w-echart" ref="barEl">
              <PaimBar :params="sjxfBarParams"></PaimBar>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {get_http} from "@/api/index";
  import PaimBar from "@/components/bar/PaimBar";
  import ctitle from "@/components/title/index";
  import liquidfill from "@/components/liquidfill/Liquidfill";
  import CustomizedPie from '@/components/pie/CustomizedPie'
  import unitSeach from "@/components/unitSeach/index";
  import sjpmBtn from "@/components/sjpmBtn/sjpmBtn";
  export default {
    name: "zrdw",
    components: {
      ctitle,
      liquidfill,
      CustomizedPie,
      unitSeach,
      PaimBar,
      sjpmBtn
    },
    data() {
      return {
        // 市级信访部门满意率排名
        sjxfBarParams: {
          data: [],
          canvasStyle: {
            width: "80%",
            height: "100%"
          }
        },
        popShow: false,
        loadFlag: false,
        // curTypeId:'drop',
        curName: '省建设厅',
        // 参评率
        cplParams:{
          data: [],
          canvasStyle: {
            width: '100%',
            height: '100%'
          },
          options:{
            radius: "50%",
          }
        },
        // 满意率
        mylParams:{
          data: [],
          canvasStyle: {
            width: '100%',
            height: '100%'
          },
          options:{
            radius: "50%",
          }
        },
        // 参评情况
        cpqkPieParams: {
          canvasStyle: {
            width: '100%',
            height: '100%'
          },
          options: {
            colors:['rgb( 154, 5, 245 )','rgb( 68, 240, 233 )','rgb( 12, 129, 254 )','rgb( 235, 131, 124 )'],   //环形颜色
            unti: '件',
            labelColor: '#fff',
            labelFontSize: 16, //21
            labelLineSmooth: 0,
            labelLineHeight: 23,
            radius: '70%'
          },
          data: [
              {value: 70, name: '已评价'},
              {value: 12, name: '超期未评价'},
              {value: 18, name: '待评价'}
          ].sort(function (a, b) { return a.value - b.value; }),
        },
        // 评价情况
        pjqkPieParams: {
          canvasStyle: {
            width: '100%',
            height: '100%'
          },
          options: {
            colors:['rgb( 154, 5, 245 )','rgb( 235, 131, 124 )','rgb( 68, 240, 233 )','rgb( 12, 129, 254 )',],   //环形颜色
            unti: '件',
            labelColor: '#fff',
            labelFontSize: 16, //21
            labelLineSmooth: 0,
            labelLineHeight: 23,
            radius: '70%'
          },
          data: [
              {value: 70, name: '已评价'},
              {value: 12, name: '超期未评价'},
              {value: 18, name: '待评价'}
          ].sort(function (a, b) { return a.value - b.value; }),
        },
        // 市级信访部门满意率排名
        mylPieParams:{
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          data: [],
          options: {
            type: 'pie',
            colors: this.$STANDARD.pieMoreColors,
            radius: ["60%", "75%"],
            center: ["50%", "50%"],
            centerRadius: ["0", "0"],
            labelRich: {
              b: this.fontSize(0.2),
              d: this.fontSize(0.22),
              bHeight: this.fontSize(0.3)
            },
            length: 20,
            length2: 50,
            tooltip: {
              show: true
            },
            bRichPadding: [30, -50, 0, -50],
            borderWidth: 5,
            itemLabelShow: true, //数据扇区label是否显示
            //图例
            legend: {
              show: false
            },
            rich: {
              bfontsize: "12%" //文字大小
            },
            labelSize: "22%", // label字号
            rollFlag: true
          }
        }
      };
    },
    mounted() {
      this.getAPI(this.date, this.curName);
    },
    methods: {
      handlePopShow() {
        this.popShow = true;
      },
      // 点击弹框后触发
      popClick(name, erjiName) {
        if (name === '') {
          this.curName = '省建设厅';
        } else {
          if (erjiName === '') {
            this.curName = name;
          } else {
            this.curName = name + '-' + erjiName;
          }
        }
        this.getAPI(this.date, this.curName);
      },
      getAPI(date, curName) {
        // 2.参评情况&main_name=满意,不满意,基本满意,超期未评价
        get_http("qjywjkMydjkZrdwCpqk", {
          main_name: '已评价,超期未评价',
          zrdw: curName,
          day: `[${date.startDate},${date.endDate}]`,
          full: true
        })
        .then(res => {
          this.cpqkPieParams.data = res.data.data;
          this.cplParams.sum = this.sum(this.cpqkPieParams.data)
        })
        .catch(err => {
          console.log(err);
        });
        // 1.参评率&main_name=已评价,待评价,超期未评价
        get_http("qjywjkMydjkZrdwCpl", {
          main_name: '已评价',
          zrdw: curName,
          day: `[${date.startDate},${date.endDate}]`,
          full: true
        })
        .then(res => {
          let pjData = res.data.data;
          let cpjvalue = pjData[0].value === null ? 0 : pjData[0].value;
          let proportion = 0;
          if (this.cplParams.sum) {
            proportion = cpjvalue / this.cplParams.sum
          } else {
            proportion = 0
          }
          this.cplParams.data = [{value: proportion}]
          // error：值为null 组件的toFixed报错  
          // this.cplParams.data = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });

        // 4.评价情况main_name=满意,不满意,基本满意,超期未评价
        get_http("qjywjkMydjkZrdwPjqk", {
          main_name: '满意,不满意,基本满意',
          zrdw: curName,
          day: `[${date.startDate},${date.endDate}]`,
          full: true
        })
        .then(res => {
          this.pjqkPieParams.data = res.data.data;
          this.mylParams.sum = this.sum(this.pjqkPieParams.data)
        })
        .catch(err => {
          console.log(err);
        });

        // 3.满意率
        get_http("qjywjkMydjkZrdwMyl", {
          main_name: '满意,基本满意',
          zrdw: curName,
          day: `[${date.startDate},${date.endDate}]`,
          full: true
        })
        .then(res => {
          let myData = res.data.data;
          // 计算满意的和
          let mySum = this.sum(myData)
          let proportion = 0;
          // 总满意率是否为0
          if (this.mylParams.sum) {
            proportion = mySum / this.mylParams.sum
          } else {
            proportion = 0
          }
          this.mylParams.data = [{value: proportion}]
          // this.mylParams.data = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });
        
        this.getSjxfAPI(date)
      },
      getSjxfAPI(date) {
        // 5.市级信访部门满意率排名
        get_http("qjywjkMydjkZrdwSjxfpm", {data: date})
          .then(res => {
            this.sjxfBarParams.data = res.data.data.sort((a, b) => a.value - b.value);
          })
          .catch(err => {
            console.log(err);
          });
      },
      fontSize(res) {
        let docEl = document.documentElement,
          clientWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
        if (!clientWidth) return;
        let fontSize = 100 * (clientWidth / 1920);
        return res * fontSize;
      },
      // 求和方法
      sum(arr) {
        let sum = 0
        for(let i=0;i<arr.length;i++) {
          sum += arr[i].value
        }
        return sum
      }
    },
    computed: {
      date() {
        return this.$store.state.date;
      }
    },
    watch: {
      date(val) {
        this.getAPI(val, this.curName);
      }
    }
  };
</script>

<style lang="stylus" scoped>
  .wrapper
    position relative
    top 0.8rem
    height calc(100% - 0.8rem)
    .w-seach
      position absolute
      top -78px
      right 113px
      z-index 1000
      box-sizing border-box
      padding-left 42px
      width 270px
      line-height 70px
      font-size 22px
      color #cbfbff
      background url("../../../assets/seach-bg3.png") no-repeat
      background-size 100% 100%
      cursor pointer
      p
        width 150px
        text-align center
        white-space nowrap
        overflow hidden
        text-overflow ellipsis
    .w-popup
      position fixed
      top 0
      left 0
      right 0
      bottom 0
      background rgba(0, 0, 0, 0.4)
      z-index 1005
      font-size 0
    .content
      width 100%
      height 100%
      display flex
      flex-direction row
      padding .2rem .3rem
      padding-bottom 0
      box-sizing border-box
      justify-content space-around
      .c-left .c-center .c-right
        display flex
        justify-content center
        box-sizing border-box
      .c-left
        flex 1
        border-right 1px #fff dashed
        display flex
        flex-direction column
        margin-top 0.2rem
        .m-title
          margin-left 0.8rem
        .cpl
          display flex
          flex 1
          justify-content center
          flex-direction center
          position relative
          .cpl-font
            position absolute
            bottom 0.4rem
            text-align center
            color #ffffff
            font-size 0.2rem
        .cpl3d
          width 100%
          div
            width 100%
            height 100%
      .c-center
        flex 1
        display flex
        flex-direction column
        border-right 1px #fff dashed
        margin-top 0.2rem
        .m-title
          margin-left 0.6rem
        .cpl
          display flex
          flex 1
          justify-content center
          flex-direction center
          position relative
          .cpl-font
            position absolute
            bottom 0.4rem
            text-align center
            color #ffffff
            font-size 0.2rem
        .cpl3d
          width 100%
          div
            width 100%
            height 100%
      .c-right
        flex 1
        display flex
        flex-direction column
        .m-title
          margin-left 0.7rem
          margin-top 0.2rem
        .cpl
          display flex
          flex 1
          flex-direction column
          margin-top 0.1rem
        .w-echart
          margin-top 0.3rem
          justify-content center
          display flex
          flex: 1
      </style>
